}

#app > main {
  background-color: var(--bg);
  flex-direction: column;
  flex-grow: 1;
  display: flex;
  overflow: auto;
}

@media screen and (min-width: 42em) {
  #app > main {
    max-width: none;
    background-color: #0000;
    padding-left: calc(50% - 20em);
    padding-right: calc(50% - 20em);
  }

  #app > main:before {
    content: " ";
    background-color: var(--bg);
    z-index: -1;
    display: block;
    position: absolute;
    inset: 0 calc(50% - 21em);
  }
}

#app > main.messageOnly {
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#app > main:not(.messageOnly) > :first-child {
  background-color: var(--bg);
  z-index: 1;
